@mixin clock-size {
  width: $clock-size;
  height: $clock-size;
}

@mixin center-size {
  width: $center-size;
  height: $center-size;
}

@mixin arrows-properties {
  width: $arrow-width;
  border-radius: $arrow-width;
  transform-origin: bottom;
}
@mixin arrows-position {
  position: absolute;
  left: $left-position;
  bottom: 50%;
}
